@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  line-height: 1.6;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  max-width: 100%;
}

body {
  height: 1000px;
  overflow-y: scroll;
}

input,
button {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
}

a[href] {
  color: var(--text-color);
  text-decoration: none;
}
